import { useState } from 'react';
import { Button } from './ui/button';
import { Input } from './ui/input';
import { Label } from './ui/label';
import { Calendar, CheckCircle2, Sparkles } from 'lucide-react';
import { motion, AnimatePresence } from 'motion/react';

interface WebinarRegistrationFormProps {
  onRegistrationComplete?: () => void;
}

export function WebinarRegistrationForm({ onRegistrationComplete }: WebinarRegistrationFormProps) {
  const [isSubmitted, setIsSubmitted] = useState(false);
  const [isAutoFilling, setIsAutoFilling] = useState(false);
  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    email: '',
    specialty: '',
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // Mock submission
    setIsSubmitted(true);
    
    // Trigger callback after animation completes and user has moment to see success
    setTimeout(() => {
      onRegistrationComplete?.();
    }, 3500);
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const typeText = async (text: string, fieldName: keyof typeof formData) => {
    for (let i = 0; i <= text.length; i++) {
      await new Promise(resolve => setTimeout(resolve, 50));
      setFormData(prev => ({ ...prev, [fieldName]: text.slice(0, i) }));
    }
  };

  const handleFirstNameClick = async () => {
    if (isAutoFilling || formData.firstName !== '') return;
    
    setIsAutoFilling(true);
    
    // Clear all fields first
    setFormData({
      firstName: '',
      lastName: '',
      email: '',
      specialty: '',
    });

    // Type each field in sequence
    await typeText('Aaron', 'firstName');
    await new Promise(resolve => setTimeout(resolve, 200));
    
    await typeText('Morita', 'lastName');
    await new Promise(resolve => setTimeout(resolve, 200));
    
    await typeText('aaron.morita1@gmail.com', 'email');
    await new Promise(resolve => setTimeout(resolve, 200));
    
    await typeText('Dermatology', 'specialty');
    
    setIsAutoFilling(false);
  };

  return (
    <div className="bg-white rounded-2xl p-8 shadow-xl relative overflow-hidden">
      <AnimatePresence mode="wait">
        {isSubmitted ? (
          <motion.div
            key="success"
            initial={{ scale: 0.8, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            transition={{ 
              type: "spring",
              stiffness: 200,
              damping: 20
            }}
            className="text-center py-4"
          >
            {/* Animated particles */}
            {[...Array(12)].map((_, i) => (
              <motion.div
                key={i}
                initial={{ scale: 0, opacity: 1, x: 0, y: 0 }}
                animate={{ 
                  scale: [0, 1, 0],
                  opacity: [0, 1, 0],
                  x: Math.cos((i / 12) * Math.PI * 2) * 100,
                  y: Math.sin((i / 12) * Math.PI * 2) * 100,
                }}
                transition={{ 
                  duration: 1.5,
                  delay: 0.2,
                  ease: "easeOut"
                }}
                className="absolute top-1/2 left-1/2 w-2 h-2 bg-[#7B2DFF] rounded-full"
                style={{ transformOrigin: 'center' }}
              />
            ))}

            {/* Success icon with pulse */}
            <motion.div 
              className="flex justify-center mb-6 relative"
              initial={{ scale: 0 }}
              animate={{ scale: 1 }}
              transition={{ 
                type: "spring",
                stiffness: 200,
                damping: 15,
                delay: 0.1
              }}
            >
              <motion.div
                animate={{ 
                  boxShadow: [
                    '0 0 0 0 rgba(123, 45, 255, 0.4)',
                    '0 0 0 20px rgba(123, 45, 255, 0)',
                    '0 0 0 0 rgba(123, 45, 255, 0)'
                  ]
                }}
                transition={{ 
                  duration: 2,
                  repeat: Infinity,
                  repeatDelay: 0.5
                }}
                className="w-20 h-20 bg-gradient-to-br from-[#7B2DFF] to-[#9F5AFF] rounded-full flex items-center justify-center relative"
              >
                <CheckCircle2 className="w-10 h-10 text-white relative z-10" />
                
                {/* Sparkle effects */}
                <motion.div
                  initial={{ opacity: 0, scale: 0 }}
                  animate={{ opacity: [0, 1, 0], scale: [0, 1, 0], rotate: 180 }}
                  transition={{ duration: 1, delay: 0.5 }}
                  className="absolute -top-2 -right-2"
                >
                  <Sparkles className="w-5 h-5 text-[#7B2DFF]" />
                </motion.div>
                <motion.div
                  initial={{ opacity: 0, scale: 0 }}
                  animate={{ opacity: [0, 1, 0], scale: [0, 1, 0], rotate: -180 }}
                  transition={{ duration: 1, delay: 0.7 }}
                  className="absolute -bottom-2 -left-2"
                >
                  <Sparkles className="w-4 h-4 text-[#9F5AFF]" />
                </motion.div>
              </motion.div>
            </motion.div>

            <motion.div
              initial={{ y: 20, opacity: 0 }}
              animate={{ y: 0, opacity: 1 }}
              transition={{ delay: 0.3 }}
            >
              <h3 className="text-[#030213] mb-3">Registration Confirmed!</h3>
              <p className="text-gray-600 leading-relaxed">
                Thank you for registering. We've sent the webinar details to <br/>
                <span className="text-[#7B2DFF]">{formData.email}</span>
              </p>
              
              <motion.div
                initial={{ opacity: 0, y: 10 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.6 }}
                className="mt-6 p-4 bg-gradient-to-br from-[#7B2DFF]/5 to-[#9F5AFF]/5 rounded-xl border border-[#7B2DFF]/20"
              >
                <p className="text-sm text-gray-700">
                  <Calendar className="w-4 h-4 inline mr-2 text-[#7B2DFF]" />
                  October 15, 2025 at 2:00 PM ET
                </p>
              </motion.div>
            </motion.div>
          </motion.div>
        ) : (
          <motion.div
            key="form"
            initial={{ opacity: 1 }}
            exit={{ 
              scale: 0.95,
              opacity: 0,
              filter: "blur(4px)"
            }}
            transition={{ duration: 0.3 }}
          >
            <div className="flex items-center gap-3 mb-6">
              <div className="w-12 h-12 bg-gradient-to-br from-[#7B2DFF] to-[#9F5AFF] rounded-xl flex items-center justify-center">
                <Calendar className="w-6 h-6 text-white" />
              </div>
              <div>
                <h3 className="text-[#030213]">Upcoming Webinar</h3>
                <p className="text-sm text-gray-600">October 15, 2025 | 2:00 PM ET</p>
              </div>
            </div>
            
            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="firstName">First Name</Label>
                  <Input
                    id="firstName"
                    name="firstName"
                    value={formData.firstName}
                    onChange={handleChange}
                    onClick={handleFirstNameClick}
                    required
                    className="border-gray-300 cursor-pointer"
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="lastName">Last Name</Label>
                  <Input
                    id="lastName"
                    name="lastName"
                    value={formData.lastName}
                    onChange={handleChange}
                    required
                    className="border-gray-300"
                  />
                </div>
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="email">Email Address</Label>
                <Input
                  id="email"
                  name="email"
                  type="email"
                  value={formData.email}
                  onChange={handleChange}
                  required
                  className="border-gray-300"
                />
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="specialty">Medical Specialty</Label>
                <Input
                  id="specialty"
                  name="specialty"
                  value={formData.specialty}
                  onChange={handleChange}
                  required
                  placeholder="e.g., Dermatology, Immunology"
                  className="border-gray-300"
                />
              </div>

              <Button 
                type="submit" 
                className="w-full bg-[#7B2DFF] hover:bg-[#6624D9] text-white h-12"
              >
                Reserve Your Spot
              </Button>
              
              <p className="text-xs text-gray-500 text-center">
                By registering, you agree to receive communications about AMLI
              </p>
            </form>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
}
