import React, { useState, useEffect, useRef } from 'react';
import { motion, AnimatePresence } from 'motion/react';
import { MessageCircle, X, Send } from 'lucide-react';
import { Button } from './ui/button';
import { Textarea } from './ui/textarea';
import { Badge } from './ui/badge';
import { AmliLogo } from './AmliLogo';

interface Message {
  id: number;
  text: string;
  sender: 'ai' | 'user';
  timestamp: Date;
}

interface AmliAiChatProps {
  isVisible?: boolean;
  onDemoModeActivate?: () => void;
  isDemoMode?: boolean;
}

export function AmliAiChat({ isVisible = false, onDemoModeActivate, isDemoMode = false }: AmliAiChatProps) {
  const [isOpen, setIsOpen] = useState(false);
  const [messages, setMessages] = useState<Message[]>([]);
  const [inputValue, setInputValue] = useState('');
  const [isTyping, setIsTyping] = useState(false);
  const [conversationStage, setConversationStage] = useState(0);
  const [hasAbandoned, setHasAbandoned] = useState(false);
  const [isStreamingQ4, setIsStreamingQ4] = useState(false);
  const [hasStreamedQ4, setHasStreamedQ4] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const streamingTimeoutRef = useRef<NodeJS.Timeout | null>(null);
  const textareaRef = useRef<HTMLTextAreaElement>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages, isTyping]);

  // Auto-resize textarea as content changes
  useEffect(() => {
    if (textareaRef.current) {
      textareaRef.current.style.height = '44px';
      const scrollHeight = textareaRef.current.scrollHeight;
      textareaRef.current.style.height = Math.min(scrollHeight, 120) + 'px';
    }
  }, [inputValue]);

  // Clean up streaming timeout on unmount
  useEffect(() => {
    return () => {
      if (streamingTimeoutRef.current) {
        clearTimeout(streamingTimeoutRef.current);
      }
    };
  }, []);

  // Send abandonment event to SaaS platform
  const sendAbandonmentEvent = () => {
    if (hasAbandoned) return;
    
    setHasAbandoned(true);
    console.log(' ABANDONMENT EVENT TRIGGERED ');
    console.log({
      event: 'chat_abandoned',
      timestamp: new Date().toISOString(),
      conversationStage,
      messageCount: messages.length,
      lastMessage: messages[messages.length - 1],
      userEmail: 'aaron.morita1@gmail.com',
      chatDuration: messages.length > 0 ? 
        (new Date().getTime() - messages[0].timestamp.getTime()) / 1000 : 0,
      platform: 'AMLI AI powered by Agentforce'
    });

    // Mock API call to SaaS platform
    fetch('/api/events/chat-abandonment', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        event: 'chat_abandoned',
        timestamp: new Date().toISOString(),
        conversationStage,
        messageCount: messages.length,
        userProfile: {
          email: 'aaron.morita1@gmail.com',
          specialty: 'Dermatology'
        }
      })
    }).catch(() => console.log('Mock API call - no actual endpoint'));
  };

  // Initial greeting when chat opens
  useEffect(() => {
    if (isOpen && messages.length === 0) {
      setTimeout(() => {
        setIsTyping(true);
        setTimeout(() => {
          setIsTyping(false);
          setMessages([{
            id: 1,
            text: "Hello Dr. Morita! I'm AMLI AI, your intelligent assistant for Amlitelimab information. How can I help you today?",
            sender: 'ai',
            timestamp: new Date()
          }]);
          setConversationStage(1);
        }, 2200);
      }, 600);
    }
  }, [isOpen]);

  // AI responses based on stage
  const getAiResponse = (stage: number): string => {
    switch (stage) {
      case 1:
        return "Excellent question! In the Phase III trials with over 1,200 patient-years of exposure, AMLI demonstrated a well-characterized safety profile. The most common adverse events were generally mild to moderate in severity and included: injection site reactions (12.3% vs 8.1% placebo), headache (8.7% vs 6.2% placebo), and upper respiratory tract infections (6.4% vs 5.9% placebo). Importantly, the majority of injection site reactions resolved within 24-48 hours without intervention. The overall discontinuation rate due to adverse events was only 2.8%, demonstrating excellent tolerability.";
      case 2:
        return "Yes, comprehensive monitoring protocols are essential for patient safety. Before initiating AMLI therapy, baseline assessments should include: complete blood count with differential, comprehensive metabolic panel, and screening for latent tuberculosis and hepatitis B/C. During treatment, we recommend monitoring CBC and liver function tests every 3 months for the first year, then every 6 months thereafter. Patients should also be educated on signs of infection and instructed to report fever >38C, persistent cough, or unusual fatigue immediately. Regular dermatological assessments every 12 weeks help track therapeutic response and identify any cutaneous reactions early.";
      case 3:
        return "This is a critical safety consideration. In the pooled Phase III analysis, serious infections occurred in 2.1% of AMLI-treated patients versus 2.3% in the standard of care group, showing a comparable safety profile. The types of serious infections were consistent with the patient population and underlying disease. Notably, there were no cases of opportunistic infections, tuberculosis reactivation, or fungal infections in the AMLI group. The mechanism of actionselective OX40L blockadepreserves innate immunity, which may explain the low infection risk. Real-world safety surveillance data from the first 6 months post-approval has corroborated these findings with no new safety signals emerging.";
      default:
        return "I'd be happy to help with that. Let me pull up the specific data for you...";
    }
  };

  const handleSendMessage = () => {
    if (!inputValue.trim() || isTyping || isStreamingQ4) return;

    // Add user message
    const userMessage: Message = {
      id: Date.now(),
      text: inputValue,
      sender: 'user',
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputValue('');

    // Variable AI response times based on complexity
    const responseDelays = [4200, 5800, 6500, 3800]; // Varies from 3.8s to 6.5s
    const typingStartDelays = [900, 1100, 1300, 850]; // Delay before typing indicator appears
    
    const currentDelay = responseDelays[conversationStage - 1] || 4000;
    const currentTypingStartDelay = typingStartDelays[conversationStage - 1] || 1000;

    // Wait a moment before showing AI is typing (feels more natural)
    setTimeout(() => {
      setIsTyping(true);

      setTimeout(() => {
        setIsTyping(false);
        
        const aiMessage: Message = {
          id: Date.now() + 1,
          text: getAiResponse(conversationStage),
          sender: 'ai',
          timestamp: new Date()
        };

        setMessages(prev => [...prev, aiMessage]);
        setConversationStage(prev => prev + 1);
      }, currentDelay); // Variable realistic AI response times
    }, currentTypingStartDelay); // Delay before showing typing indicator
  };

  // Stream Q4 character by character when input is clicked after Q3
  const handleInputClick = () => {
    // First click after conversation stage 4: Stream the question
    if (conversationStage === 4 && !isStreamingQ4 && !hasStreamedQ4 && inputValue === '') {
      setIsStreamingQ4(true);
      const q4Text = "Can you provide guidance on dose adjustment protocols if a patient experiences an adverse event?";
      const interruptAt = 60; // Stop at "Can you provide guidance on dose adjustment protoc" - mid-word "protocols"
      
      let currentIndex = 0;
      
      const streamCharacter = () => {
        if (currentIndex < interruptAt) {
          setInputValue(q4Text.substring(0, currentIndex + 1));
          currentIndex++;
          streamingTimeoutRef.current = setTimeout(streamCharacter, 40); // 40ms per character for visible streaming
        } else {
          // Abruptly stop - don't complete the word
          setIsStreamingQ4(false);
          setHasStreamedQ4(true);
        }
      };

      streamCharacter();
      return;
    }
    
    // Second click after streaming is complete: Trigger demo mode
    if (conversationStage === 4 && hasStreamedQ4 && !isStreamingQ4) {
      if (onDemoModeActivate) {
        onDemoModeActivate();
      }
    }
  };

  const handleClose = () => {
    // Clean up any streaming
    if (streamingTimeoutRef.current) {
      clearTimeout(streamingTimeoutRef.current);
    }
    
    // Trigger abandonment event if chat is closed with active conversation
    if (messages.length > 0 && conversationStage < 5) {
      sendAbandonmentEvent();
    }
    setIsOpen(false);
  };

  // Don't render anything if demo mode is active
  if (isDemoMode) {
    return null;
  }

  return (
    <>
      {/* Floating Chat Button */}
      <AnimatePresence>
        {!isOpen && isVisible && (
          <motion.div
            initial={{ scale: 0, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            exit={{ scale: 0, opacity: 0 }}
            transition={{ type: "spring", stiffness: 260, damping: 20 }}
            className="fixed bottom-6 right-6 z-50"
          >
            <motion.button
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              onClick={() => setIsOpen(true)}
              className="relative bg-gradient-to-br from-[#7B2DFF] to-[#9F5AFF] text-white p-4 rounded-full shadow-2xl hover:shadow-purple-500/50 transition-shadow"
            >
              {/* Pulse animation */}
              <motion.div
                animate={{ 
                  scale: [1, 1.2, 1],
                  opacity: [0.5, 0, 0.5]
                }}
                transition={{ 
                  duration: 2,
                  repeat: Infinity,
                  ease: "easeInOut"
                }}
                className="absolute inset-0 bg-[#7B2DFF] rounded-full"
              />
              
              <MessageCircle className="w-6 h-6 relative z-10" />
              
              {/* New message indicator */}
              <motion.div
                initial={{ scale: 0 }}
                animate={{ scale: 1 }}
                transition={{ delay: 0.5 }}
                className="absolute -top-1 -right-1 w-4 h-4 bg-red-500 rounded-full border-2 border-white"
              />
            </motion.button>
            
            {/* Tooltip */}
            <motion.div
              initial={{ opacity: 0, x: 20 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ delay: 0.8 }}
              className="absolute right-full mr-3 top-1/2 -translate-y-1/2 bg-[#030213] text-white px-3 py-2 rounded-lg text-sm whitespace-nowrap shadow-lg"
            >
              Ask AMLI AI more about safety!
              <div className="absolute right-0 top-1/2 -translate-y-1/2 translate-x-full w-0 h-0 border-t-4 border-b-4 border-l-4 border-transparent border-l-[#030213]" />
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Chat Window */}
      <AnimatePresence>
        {isOpen && isVisible && (
          <motion.div
            initial={{ opacity: 0, y: 100, scale: 0.8 }}
            animate={{ opacity: 1, y: 0, scale: 1 }}
            exit={{ opacity: 0, y: 100, scale: 0.8 }}
            transition={{ type: "spring", stiffness: 300, damping: 30 }}
            className="fixed bottom-6 right-6 w-[420px] h-[600px] bg-white rounded-2xl shadow-2xl z-50 flex flex-col overflow-hidden border border-gray-200"
          >
            {/* Header */}
            <div className="bg-gradient-to-br from-[#C8B3FF] to-[#E5D9FF] p-5 text-[#030213] relative overflow-hidden">
              {/* Decorative background elements */}
              <div className="absolute top-0 right-0 w-40 h-40 bg-white/40 rounded-full blur-3xl" />
              <div className="absolute -bottom-10 -left-10 w-32 h-32 bg-[#7B2DFF]/10 rounded-full blur-2xl" />
              
              <div className="flex items-center justify-between relative z-10">
                <div className="flex items-center gap-4">
                  {/* Enhanced Logo Container */}
                  <div className="relative">
                    <div className="absolute inset-0 bg-[#7B2DFF]/20 rounded-full blur-lg" />
                    <div className="relative w-14 h-14 bg-white rounded-full flex items-center justify-center p-2.5">
                      <AmliLogo variant="icon-only" size="sm" className="h-full w-full object-contain" />
                    </div>
                  </div>
                  
                  <div>
                    <h3 className="text-[#030213] text-xl mb-1">AMLI AI</h3>
                    <Badge className="bg-white/60 text-[#7B2DFF] border-0 text-xs backdrop-blur-sm">
                      Powered by Agentforce
                    </Badge>
                  </div>
                </div>
                <Button
                  onClick={handleClose}
                  variant="ghost"
                  size="icon"
                  className="text-[#030213] hover:bg-white/30 h-9 w-9 rounded-xl"
                >
                  <X className="w-5 h-5" />
                </Button>
              </div>
            </div>

            {/* Messages */}
            <div className="flex-1 overflow-y-auto p-4 space-y-4 bg-gray-50">
              {messages.map((message, index) => (
                <motion.div
                  key={message.id}
                  initial={{ opacity: 0, y: 10 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ delay: index * 0.05 }}
                  className={`flex ${message.sender === 'user' ? 'justify-end' : 'justify-start'}`}
                >
                  <div className={`max-w-[80%] rounded-2xl px-4 py-3 ${
                    message.sender === 'user'
                      ? 'bg-[#7B2DFF] text-white'
                      : 'bg-white text-gray-800 border border-gray-200'
                  }`}>
                    {message.sender === 'ai' && (
                      <div className="flex items-center gap-2.5 mb-2">
                        <div className="w-6 h-6 bg-gradient-to-br from-[#7B2DFF]/20 to-[#9F5AFF]/20 backdrop-blur-sm rounded-full flex items-center justify-center p-1 flex-shrink-0">
                          <AmliLogo variant="icon-only" size="sm" className="h-full w-full object-contain" />
                        </div>
                        <span className="text-xs text-gray-500">AMLI AI</span>
                      </div>
                    )}
                    <p className="text-sm leading-relaxed">{message.text}</p>
                    <p className={`text-xs mt-1 ${
                      message.sender === 'user' ? 'text-white/60' : 'text-gray-400'
                    }`}>
                      {message.timestamp.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                    </p>
                  </div>
                </motion.div>
              ))}

              {/* Typing indicator */}
              {isTyping && (
                <motion.div
                  initial={{ opacity: 0, y: 10 }}
                  animate={{ opacity: 1, y: 0 }}
                  className="flex justify-start"
                >
                  <div className="bg-white border border-gray-200 rounded-2xl px-4 py-3 max-w-[80%]">
                    <div className="flex items-center gap-2.5 mb-2">
                      <div className="w-6 h-6 bg-gradient-to-br from-[#7B2DFF]/20 to-[#9F5AFF]/20 backdrop-blur-sm rounded-full flex items-center justify-center p-1 flex-shrink-0">
                        <AmliLogo variant="icon-only" size="sm" className="h-full w-full object-contain" />
                      </div>
                      <span className="text-xs text-gray-500">AMLI AI</span>
                    </div>
                    <div className="flex items-center gap-2">
                      <span className="text-xs text-gray-400">analyzing clinical data</span>
                      <div className="flex gap-1">
                        {[0, 1, 2].map((i) => (
                          <motion.div
                            key={i}
                            animate={{ y: [0, -5, 0] }}
                            transition={{ 
                              duration: 0.6,
                              repeat: Infinity,
                              delay: i * 0.1
                            }}
                            className="w-1.5 h-1.5 bg-[#7B2DFF] rounded-full"
                          />
                        ))}
                      </div>
                    </div>
                  </div>
                </motion.div>
              )}

              <div ref={messagesEndRef} />
            </div>

            {/* Input */}
            <div className="p-4 bg-white border-t border-gray-200">
              <div className="flex gap-2 items-end">
                <Textarea
                  ref={textareaRef}
                  value={inputValue}
                  onChange={(e) => setInputValue(e.target.value)}
                  onClick={handleInputClick}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' && !e.shiftKey) {
                      e.preventDefault();
                      handleSendMessage();
                    }
                  }}
                  placeholder="Ask about AMLI safety, efficacy..."
                  className="flex-1 border-gray-300 focus-visible:ring-[#7B2DFF] min-h-[44px] max-h-[120px] resize-none overflow-y-auto"
                  disabled={isStreamingQ4}
                  rows={1}
                />
                <Button
                  onClick={handleSendMessage}
                  disabled={!inputValue.trim() || isTyping || isStreamingQ4}
                  className="bg-[#7B2DFF] hover:bg-[#6624D9] text-white h-[44px] w-[44px]"
                  size="icon"
                >
                  <Send className="w-4 h-4" />
                </Button>
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </>
  );
}